import { isNil } from "ramda";
import { AuthService } from "./../../services/auth.service";
import { Component } from "@angular/core";
import { Router } from "@angular/router";
import { DatePipe } from "@angular/common";
import { CognitoPhonePipe } from "../../util/cognito-phone.pipe";
import { RegistrationUser } from "./registration.model";
import { TitleCasePipe } from "@angular/common";
import { LoginState } from "../../model/login-state.model";

declare var jQuery: any;

/**
 * This component is responsible for displaying and controlling
 * the registration of the user.
 */
@Component({
	selector: "app-register-cognito",
	templateUrl: "./registration.template.html",
	styleUrls: ["./registration.style.scss"]
})
export class RegisterComponent {
	registrationUser: RegistrationUser;
	router: Router;
	errorMessage: string;
	phonePipe: CognitoPhonePipe;

	phoneMask = {
		mask: [
			/[0-9]/,
			/[0-9]/,
			/[0-9]/,
			"-",
			/[0-9]/,
			/[0-9]/,
			/[0-9]/,
			"-",
			/[0-9]/,
			/[0-9]/,
			/[0-9]/,
			/[0-9]/
		]
	};

	constructor(
		router: Router,
		private auth: AuthService,
		private titlecasePipe: TitleCasePipe
	) {
		this.router = router;
		this.phonePipe = new CognitoPhonePipe();
		this.onInit();
	}

	onInit() {
		this.registrationUser = new RegistrationUser();
		this.errorMessage = null;
		jQuery(document).ready(function() {
			jQuery("#userName").focus();
		});
	}

	onRegister() {
		this.errorMessage = null;

		let transformUser = new RegistrationUser();
		transformUser.username = this.registrationUser.username;
		transformUser.firstname = this.registrationUser.firstname;
		transformUser.name = this.registrationUser.name;
		transformUser.email = this.registrationUser.email;
		transformUser.password = this.registrationUser.password;
		// transformUser.phone_number = this.registrationUser.phone_number;
		// transformUser.awsPhone = this.phonePipe.transform(
		// 	this.registrationUser.phone_number
		// );
		let handler = this;
		jQuery("#frm-register")
			.parsley()
			.validate();
		if (
			jQuery("#frm-register")
				.parsley()
				.isValid()
		) {
			// this.auth.amp_signUp(transformUser).subscribe(
			// 	result => {
			// 		let status = this.auth.amp_getLoginState();
			// 		status.signUp(transformUser);
			// 		this.router.navigate(["/login/confirmRegistration"]);
			// 		// this.onSubmitConfirmation(result);
			// 	},
			// 	error => {
			// 		if (!isNil(error.message)) {
			// 			handler.errorMessage = error.message;
			// 		}
			// 		console.log(error);
			// 	}
			// );
		}
	}

	capitalizeNames() {
		this.registrationUser.name = this.titlecasePipe.transform(
			this.registrationUser.name
		);
		this.registrationUser.firstname = this.titlecasePipe.transform(
			this.registrationUser.firstname
		);
	}

	lowercaseEmail() {
		this.registrationUser.email = !isNil(this.registrationUser.email)
			? this.registrationUser.email.toLowerCase()
			: "";
		this.registrationUser.confirmEmail = !isNil(
			this.registrationUser.confirmEmail
		)
			? this.registrationUser.confirmEmail.toLowerCase()
			: "";
	}

	replaceNil(text: string) {
		if (isNil(text)) {
			return "";
		}
		return text;
	}

	onSubmitConfirmation(value: any) {}
}
