import {
	AfterViewInit,
	Component,
	OnDestroy,
	OnInit,
	ViewEncapsulation
} from "@angular/core";
import { Router } from "@angular/router";
import { BsModalService } from "ngx-bootstrap/modal";
import { BsModalRef } from "ngx-bootstrap/modal/bs-modal-ref.service";
import { LocalStorageService } from "ngx-webstorage";
import { isNil } from "ramda";
import { Subscription } from "rxjs";
import { Credentials } from "../model/credentials.model";
import { LoginState } from "../model/login-state.model";
import { AuthService } from "../services/auth.service";
import { ProfileService } from "../services/profile.service";
import { environment } from "./../../../environments/environment";
import { OnComponentErrorHandler } from "./../../core/coreerror.handler";
import { LoginMessageBox } from "./lg-msgbox.component";
import { RegistrationUser } from "./register/registration.model";
import { AdminService } from '../../admin/services/admin.service';
import { User } from '../../admin/models/user.model';
import { ShareDataService } from '../../core/sharedata.service';

declare var jQuery: any;

// Step 1: User logs in
// Step 2:
@Component({
	selector: "login",
	styleUrls: ["./login.style.scss"],
	templateUrl: "./login.template.html",
	encapsulation: ViewEncapsulation.None,
	providers: [BsModalService, AdminService],
	host: {
		class: "login-page app"
	}
})
export class LoginComponent
	implements AfterViewInit, OnComponentErrorHandler, OnInit, OnDestroy {
	public model = new Credentials("rohit@test.com", "Password.01");
	public showErrorFlag: boolean;
	public alerts: Array<Object>;
	public currentYear: string;
	public loginData: any;
	public AppTitle: string;
	public loginTitle: string;
	public loggedIn: boolean;
	public isLoginButtonClicked: boolean;
	public currentUser: User;

	public busySignIn: Subscription;

	public bsModalRefSplash: BsModalRef;
	public bsModalRefMessageBox: BsModalRef;

	public isIframe: boolean;
	public busyLoginFailureSubscription: Subscription;
	public busyLoginSuccessSubscription: Subscription;

	constructor(
		private storage: LocalStorageService,
		private auth: AuthService,
		private adminService: AdminService,
		private profile: ProfileService,
		private modalService: BsModalService,
		private shareDataService: ShareDataService,
		private router: Router
	) {
		this.alerts = [
			{
				type: "warning",
				msg: '<span class="fw-semi-bold">Warning:</span> Error Logging in'
			}
		];

		this.AppTitle = "CBASDemo";
		switch (environment.stage) {
			case 'dev':
				this.AppTitle = "CBASDemo";
				break;

			case 'qa':
				this.AppTitle = "CBASDemo-QA";
				break;

			default:
				break;
		}

		this.loginTitle = "User Login";
		this.showErrorFlag = false;
		this.currentYear = new Date().getFullYear().toString();
	}

	ngOnInit(): void {

		if (localStorage.getItem('access_token_id') !== null) {
			if (this.auth.jwt_hasRole('MOS-OCD')) {
				// this.router.navigate(['app/timesheet/resplan']);
				this.router.navigate(['app/reports/current-request-reports']);
			} else {
				// this.router.navigate(['app/tdl/search']);
				this.router.navigate(['app/reports/current-request-reports']);
			}

		} else {
			this.showErrorFlag = false;
			this.isLoginButtonClicked = false;
		}
	}

	ngAfterViewInit() {
		jQuery("#uname").focus();
		this.clearLocalStorageContent();

		if (environment.stage === 'dev') {
			// this.login();
		}
	}

	ngOnDestroy() {
		if (this.busySignIn) {
			this.busySignIn.unsubscribe();
		}
	}

	onResetError(): void {
		this.showErrorFlag = false;
		this.alerts = [];
	}

	onShowError(error: any): void {
		this.showErrorFlag = true;
		this.alerts.push({ type: "warning", msg: error });
	}

	onShowBodyError(error: any): void {
		this.showErrorFlag = true;
		this.alerts.push({ type: "warning", msg: error });
	}

	clearLocalStorageContent() { }

	public displayConfirmationMessage(msgtoDisplay) {
		const initialState = {
			message: msgtoDisplay,
			title: "Need Email Verification"
		};
		this.bsModalRefMessageBox = this.modalService.show(LoginMessageBox, {
			initialState
		});
		this.bsModalRefMessageBox.content.closeBtnName = "Close";
	}

	private proceedtoLandingPage() {
		let that = this;
		this.adminService.getUserbyEmail(this.model.username)
			.subscribe(data => {
				if (data) {
					that.currentUser = data;
					that.storage.store('UserID', that.currentUser.userId);

					let loggedUserName = this.currentUser.firstName + " " + that.currentUser.lastName;
					that.storage.store('LoggedUser', loggedUserName);

					that.shareDataService.loggedUser = loggedUserName;

					if (that.auth.jwt_hasRole('ROLE_USER')) {
						// that.router.navigate(['app/timesheet/resplan']);
						that.router.navigate(['app/reports/current-request-reports']);
					} else {
						// that.router.navigate(['app/tdl/search']);
						that.router.navigate(['app/reports/current-request-reports']);
					}
				}
			});
	}


	// Step 6: Send check for verification email
	public verifySendEmail(localUser: RegistrationUser) {
		this.proceedtoLandingPage();
	}


	public login() {
		return this.jwt_login();
	}

	public jwt_login() {
		if (this.isLoginButtonClicked) {
			return true;
		}
		this.clearLocalStorageContent();
		this.onResetError();

		jQuery("#frm-login")
			.parsley()
			.validate();
		if (
			jQuery("#frm-login")
				.parsley()
				.isValid()
		) {
			let handler = this;
			this.isLoginButtonClicked = true;
			// let status: LoginState = this.auth.jwt_getLoginState();
			// Step 2: See if user is authorized to login
			this.auth.jwt_login(this.model).subscribe(
				data => {
					if (data) {
						this.auth.jwt_setAccessToken(data.accesstoken);
						this.auth.jwt_setRefreshToken(data.refreshtoken);
						this.auth.jwt_setRole(data.rolelist);
						// status.successfulSignIn();
						this.proceedtoLandingPage();
					}
				},
				error => {
					// console.error(error.code);
					if (!isNil(error.error) && !isNil(error.error.errorCode)) {
						this.onShowError(error.error.message);
					} else {
						this.onShowError(error);
					}
					this.isLoginButtonClicked = false;
				}
			);
		}
	}

	public logout() {
		this.auth.jwt_logout();
		this.router.navigate(["login"]);
	}
}
