import { CognitoPhonePipe } from "../../util/cognito-phone.pipe";

export class RegistrationUser {
	constructor() {
		this.phonePipe = new CognitoPhonePipe();
		this.firstname = "";
		this.name = "";
	}
	phonePipe: CognitoPhonePipe;
	username: string;
	firstname: string;
	name: string;
	email: string;
	confirmEmail: string;
	phone_number: string;
	awsPhone: string;
	password: string;
	confirmPassword: string;
	confirmationCode: string;
	combinedAddress: string;
	emailVerified: boolean;

	public populate(user: any) {
		if (user) {
			this.username = user.username;
			let attributes = user.attributes;
			this.name = attributes["family_name"];
			this.firstname = attributes["given_name"];
			this.email = attributes["email"];
			this.emailVerified = attributes["email_verified"];
			this.awsPhone = attributes["phone_number"];
			this.phone_number = this.phonePipe.parse(this.awsPhone);
		}
	}
}
